module.exports = ".details-page_wrapper {\r\n  background: linear-gradient(to top, #86DBFF 0%, #e0c3fc 100%);\r\n  /* hiển thị theo flex */\r\n  display: flex;\r\n  /* nội dung ở trong được canh giữa theo phương ngang */\r\n  justify-content: center;\r\n  /* những phần trong xác định vị trí theo vị trí tương đối */\r\n  position: relative;\r\n  /* min-height: 110vh; */\r\n  /* đệm trên và dưới */\r\n  padding: 2rem 0;\r\n}\r\n\r\n.details-page_wrapper-dark {\r\n  background: linear-gradient(#495CFC, #FC7DB8);\r\n}\r\n\r\n/* định dạng cho hình tròn làm nền phía sau */\r\n\r\n.background-gradient_circle {\r\n  position: absolute;\r\n  top: 0%;\r\n  right: 0%;\r\n  z-index: 0;\r\n  /* chiều cao chiếm hết màn hình */\r\n  height: 100%;\r\n  /* bề rộng chiếm hết màn hình */\r\n  width: 100%;\r\n  border-radius: 50%;\r\n  background: #FC7DB8;\r\n  /* chuyển đổi màu nền của hình tròn nền trong 4s */\r\n  /* mất 4s để có được màu nền này */\r\n  transition: background 4s ease;\r\n}\r\n\r\n.background-gradient_circle-dark {\r\n  background: #6676FF;\r\n  /* chuyển đổi màu nền của hình tròn nền trong 2s */\r\n  /* mất 2s để có được màu nền này */\r\n  transition: background 2s ease;\r\n}\r\n\r\n/* định dạng cho nút BACK ở chế độ mặc định */\r\n\r\n.back_button {\r\n  position: absolute;\r\n  top: 3rem;\r\n  left: 3.25rem;\r\n  width: 5rem;\r\n  cursor: pointer;\r\n  z-index: 2;\r\n  margin-top: 2rem;\r\n}\r\n\r\n.main-weather_card {\r\n  position: relative;\r\n  /* màu nền */\r\n  background-color: white;\r\n  height: 80%;\r\n  width: 75%;\r\n  border-radius: 1rem;\r\n  z-index: 1;\r\n}\r\n\r\n/* định dạng cho hình ảnh đại diện thành phố */\r\n\r\n.city_illustration {\r\n  width: 100%;\r\n  height: 420px;\r\n  border-radius: 1rem 1rem 0 0;\r\n  -o-object-fit: cover;\r\n     object-fit: cover;\r\n}\r\n\r\n/* định dạng cho vùng ghi thông tin thời tiết hiện tại */\r\n\r\n.header-content_wrapper {\r\n  position: absolute;\r\n  z-index: 1;\r\n  color: white;\r\n  top: 3rem;\r\n  display: grid;\r\n  /* chia làm 2 cột, mỗi cột rộng 1fr */\r\n  grid-template-columns: repeat(2, 1fr);\r\n  /* lưới chiếm 100% chiều rộng */\r\n  width: 100%;\r\n}\r\n\r\n/* định dạng cho vùng ghi thông tin thời tiết */\r\n\r\n.today-weather_container {\r\n  display: grid;\r\n  grid-template-rows: 3fr 1fr;\r\n  /* nội dung của nó nằm chính giữa theo phương ngang */\r\n  justify-items: center;\r\n  /* khoảng cách với phần lưới trên */\r\n  grid-gap: 2rem;\r\n}\r\n\r\n/* vùng ghi nhiệt độ và độ ẩm */\r\n\r\n.temp-state_container {\r\n  display: flex;\r\n  /* sắp xếp các mục theo chiều dọc */\r\n\tflex-direction: column;\r\n}\r\n\r\n.temperature_text {\r\n  font-size: 6rem;\r\n  letter-spacing: 0.75rem;\r\n}\r\n\r\n.weather-state_text {\r\n  letter-spacing: 0.5rem;\r\n  font-size: 1.15rem;\r\n  text-transform: uppercase;\r\n  margin-top: 0.25rem;\r\n}\r\n\r\n/* vùng ghi độ ẩm và gió */\r\n\r\n.hum-wind_container {\r\n  display: flex;\r\n  /* canh giữa theo phương dọc */\r\n  align-items: center;\r\n  /* lề trái là -4rem */\r\n  margin-left: -4rem;\r\n}\r\n\r\n.hum_text, .wind_text {\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.2rem;\r\n  font-size: 0.8rem;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.hum_container, .wind_container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n}\r\n\r\n.hum-wind_separator {\r\n  margin: 0 2rem;\r\n  width: 2px;\r\n  height: 2.5rem;\r\n  background-color: white;\r\n}\r\n\r\n.city-name_container {\r\n  display: flex;\r\n  /* canh giữa theo phương ngang */\r\n  justify-content: center;\r\n  /* canh giữa theo phương dọc */\r\n  align-items: center;\r\n  padding-bottom: 25%;\r\n}\r\n\r\n.city-name_underline {\r\n  border-radius: 5px;\r\n  height: 5px;\r\n  box-shadow: 0 3rem 0 0 #ffffff;\r\n}\r\n\r\n.city-name_text {\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.3rem;\r\n  font-size: 1.75rem;\r\n}\r\n\r\n/*\r\n================\r\n     BODY\r\n================\r\n*/\r\n\r\n.body-content_wrapper {\