y: flex;\r\n  flex-flow: column;\r\n  margin: 2rem 1.5rem;\r\n  align-items: center;\r\n}\r\n\r\n.day-name_text {\r\n  font-size: 1.5rem;\r\n  color: #39437a;\r\n  font-weight: bold;\r\n  text-transform: uppercase;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.forecast-condition_icon {\r\n  height: 4rem;\r\n}\r\n\r\n.day-temp_text {\r\n  font-size: 1.85rem;\r\n  color: #0c1066;\r\n  letter-spacing: 0.25rem;\r\n  margin: 0.75rem 0;\r\n  padding-left: 0.35rem;\r\n}\r\n\r\n.day-state_text {\r\n  font-size: 0.65rem;\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.2rem;\r\n  color: #2B244D;\r\n}\r\n\r\n/* khi chiều rộng màn hình <= 1279px */\r\n\r\n@media screen and (max-width: 1279px) {\r\n  .back_button {\r\n    top: 1rem;\r\n  }\r\n  \r\n  .main-weather_card {\r\n    width: 80%;\r\n  }\r\n  \r\n  .body-content_wrapper {\r\n    grid-template-columns: 1fr;\r\n    justify-items: center;\r\n  }\r\n}\r\n\r\n/* khi chiều rộng màn hình <= 959px */\r\n\r\n@media screen and (max-width: 959px) {\r\n  .header-content_wrapper {\r\n    grid-template-columns: 1fr;\r\n    grid-template-rows: 2fr 1fr;\r\n  }\r\n\r\n  .back_button {\r\n    position: static;\r\n    margin-bottom: 1rem;\r\n  }\r\n\r\n  .details-page_wrapper {\r\n    flex-flow: column;\r\n    align-items: center;\r\n    padding-bottom: 25%;\r\n  }\r\n\r\n  .hum-wind_container, .temp-state_container {\r\n    margin: 0 auto;\r\n  }\r\n\r\n  .city-name_container {\r\n    padding-bottom: 0;\r\n  }\r\n\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"